/*
background-color: #1e1e1e; // bkg-gray

border-radius: 5px; // border-radius

color: #649ad1; // blue
color: #688855; // green
color: #a7d9f9; // light blue
color: #c3ad7c; // light orange
color: #c6947c; // orange
color: #d4d4d4; // base text color
color: #bacdab; // light green

font-family: menlo, monospace; // font

padding: 0.25em;
*/

/* HTML/CSS/JS only rouge declarations */

/* Default text color and background color */
.highlight,
.highlight .w {
  background-color: #1e1e1e; // bkg-gray
  border-radius: 5px; // border-radius
  color: #d4d4d4;
  font-family: menlo, monospace; // font
  overflow: scroll;
}

.kd,
.kc,
.k,
.highlight .nt,
.highlight .cp {
  color: #649ad1; // blue
}

.highlight .c,
.highlight .ch,
.highlight .cd,
.highlight .cm,
.highlight .cp,
.highlight .cpf,
.highlight .c1,
.highlight .cs {
  color: #688855; // green
}

.highlight .nb,
.highlight .nf,
.highlight .n,
.highlight .nd,
.highlight .no,
.highlight .s {
  color: #c6947c; // orange
}

.dl,
.s2,
.highlight .nc {
  color: #c3ad7c; // light orange
}

.nx,
.highlight .nl,
.highlight .na,
.highlight .py {
  color: #a7d9f9; // light blue
}

.mi,
.highlight .m {
  color: #bacdab; // light green
}

/* Code syntax for items not classed specifically by rouge */

code {
  font-family: menlo, monospace; // font
  font-size: 0.8em;
  line-height: 1.25; // line-height

}

li {
  code {
    background-color: #1e1e1e; // bkg-gray
    border-radius: 5px; // border-radius
    color: #d4d4d4; // blue
    font-family: menlo, monospace; // font
    font-size: 0.8em;
    line-height: 1.25; // line-height
    padding-bottom: 0.25em;
    padding-left: 0.5em;
    padding-right: 0.5em;
    padding-top: 0.25em;
  }
}

pre.highlight {
  font-family: menlo, monospace; // font
  line-height: 1.25; // line-height
  padding-left: 1em;
  padding-right: 1em;
}

p {
  // In-paragraph code spans
  code {
    background-color: #1e1e1e; // bkg-gray
    border-radius: 5px; // border-radius
    color: #d4d4d4; // blue
    font-family: menlo, monospace; // font
    font-size: 0.8em;
    line-height: 1.25; // line-height
    padding-bottom: 0.25em;
    padding-left: 0.5em;
    padding-right: 0.5em;
    padding-top: 0.25em;
  }
}
